<template>
  <div>
      <el-row type="flex" justify="center">
        <el-col :span="20">
          <div style="text-align: center">
            <transition name="intro">
              <div style="margin-top: 80px;" class="intro-text" v-if="show1">嗨！你好！</div>
            </transition>
            <transition name="intro">
              <div class="intro-text" v-if="show2">欢迎来我的主页！</div>
            </transition>
            <transition name="intro">
              <div class="intro-text" v-if="show3">我叫张翊卓</div>
            </transition>
            <transition name="intro">
              <div class="intro-text" v-if="show4">很高兴认识你！</div>
            </transition>
          </div>
        </el-col>
      </el-row>
      <transition name="bus">
        <div v-if="showBus" class="bus-card">
          <div style="height: 65px;"></div>
          <transition name="line-top">
            <div v-if="showName" class="name-h-line"></div>
          </transition>
          <transition name="line-left">
            <div v-if="showName" class="name-v-l-line"></div>
          </transition>
          <transition name="line-right">
            <div v-if="showName" class="name-v-r-line"></div>
          </transition>
          <transition name="bus-card">
            <p v-if="showName" class="card-name">张翊卓</p>
          </transition>
          <transition name="line-bottom">
            <div v-if="showName" class="name-h-line"></div>
          </transition>
        </div>
      </transition>
      <transition name="info-box">
        <div v-if="showInfoBox1" class="infor-box" style="background-color: #e1d28f"></div>
      </transition>
      <transition name="info-box">
        <div v-if="showInfoBox2" class="infor-box" style="background-color: #ebaa4a"></div>
      </transition>
      <transition name="info-box">
        <div v-if="showInfoBox3" class="infor-box" style="background-color: #eb6a2d"></div>
      </transition>
      <transition name="info-box">
        <div v-if="showInfoBox4" class="infor-box" style="background-color: #a32c1c"></div>
      </transition>
      <transition name="info-box">
        <div v-if="showInfoBox5" class="infor-box" style="background-color: #563b2f"></div>
      </transition>
      <transition name="info-box">
        <div v-if="showInfoBox6" class="infor-box" style="background-color: #bdd4c7"></div>
      </transition>
  </div>
</template>

<script>
export default {
  name: 'PersonalInfo',
  mounted () {
    setTimeout(this.animationen, 1000)
  },
  data () {
    return {
      msg: 'Welcome to My World !',
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      showBus: false,
      showName: false,
      showInfoBox1: false,
      showInfoBox2: false,
      showInfoBox3: false,
      showInfoBox4: false,
      showInfoBox5: false,
      showInfoBox6: false
    }
  },
  methods: {
    animationen () {
      var t = this
      setTimeout(t._show1, 500)
      setTimeout(t._show2, 700)
      setTimeout(t._show3, 900)
      setTimeout(t._show4, 1700)
      setTimeout(t._show4, 2800)
      setTimeout(t._show3, 2900)
      setTimeout(t._show2, 3000)
      setTimeout(t._show1, 3100)
      setTimeout(t._showBus, 3500)
      setTimeout(t._showName, 4000)
      setTimeout(t._showInfoBox1, 4300)
      setTimeout(t._showInfoBox2, 4500)
      setTimeout(t._showInfoBox3, 4700)
      setTimeout(t._showInfoBox4, 4900)
      setTimeout(t._showInfoBox5, 5100)
      setTimeout(t._showInfoBox6, 5300)
    },
    _show1 () {
      this.show1 = !this.show1
    },
    _show2 () {
      this.show2 = !this.show2
    },
    _show3 () {
      this.show3 = !this.show3
    },
    _show4 () {
      this.show4 = !this.show4
    },
    _showBus () {
      this.showBus = !this.showBus
    },
    _showName () {
      this.showName = !this.showName
    },
    _showInfoBox1 () {
      this.showInfoBox1 = !this.showInfoBox1
    },
    _showInfoBox2 () {
      this.showInfoBox2 = !this.showInfoBox2
    },
    _showInfoBox3 () {
      this.showInfoBox3 = !this.showInfoBox3
    },
    _showInfoBox4 () {
      this.showInfoBox4 = !this.showInfoBox4
    },
    _showInfoBox5 () {
      this.showInfoBox5 = !this.showInfoBox5
    },
    _showInfoBox6 () {
      this.showInfoBox6 = !this.showInfoBox6
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import './../assets/css/personalInfo.css';
</style>



